body {
    height: 100vh;
   
    background-color:#daebf85d;
  }
.contact{
    padding-bottom: 2rem;
}
.contact .heading{
    text-align: center;
   
  
    font-weight: 800;
   padding-top: 3rem;
    font-family: "Poppins", sans-serif;
}
.contact .heading span{
    color: rgb(115, 3, 167);
  }
  .contact .heading i{
    padding-right: 0.7rem;
  }
  .contact .row.single-form{
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
    border-radius: 1.5rem;
    background: #fff;
    margin-top: 2rem;

}
.form-group .field {
    height: 50px;
  
    position: relative;
  
    width: 100%;
  }
form i {
    position: absolute;
    top: 50%;
    left: 18px;
    color: rgb(51, 51, 51);
    font-size: 17px;
    pointer-events: none;
    margin-right: 10rem;
    transform: translateY(-50%);
  }
  form .field input,
form .message textarea {
 

    width: 100%;
    height: 100%;
    outline: none;
    padding: 0 16px 0 48px;
    font-size: 16px;
    font-family: "Poppins", sans-serif;
    border-radius: 5px;
    border: 1px solid rgb(51, 51, 51);
    background: #e5ecfb;
 
  } 
  .field input::placeholder,
  .message textarea::placeholder {
    color: rgb(51, 51, 51);
  }
  
  .field input:focus,
  .message textarea:focus {
    padding-left: 47px;
    border: 2px solid rgb(115, 3, 167);
  }
  
  .field input:focus~i,
  .message textarea:focus~i {
    color: rgb(115, 3, 167);
  }
  form .message {
    position: relative;
 
    width: 100%;
  }
  form .message i {
    top: 25px;
    font-size: 20px;
    left: 15px;
  }
  form .message textarea {
    min-height: 130px;
    max-height: 230px;
    max-width: 100%;
    min-width: 100%;
    padding: 12px 20px 0 48px;
  }
  .content .contact-btn{
    text-align: right;
  }
  .contact-btn .button{
    color: #fff;
      border: none;
      outline: none;
      font-size: 1.2rem;
      cursor: pointer;
      border-radius: 5px;
      padding: 10px 25px;
      background: #2506ad;
      box-shadow: 0px 5px 10px rgba(48, 68, 247, 0.6);
      transition: 0.3s ease;
      font-family: "Nunito", sans-serif;
  }
  .contact-btn .button:hover{
    background: #421cecf5;
  }
 

  /* contact section media queries starts */
@media (max-width: 992px) {
    .contact .img-box{
        display:none;
    }
    }
  
   
      