/* General styles for the page */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
  }
  
  /* Styles for the comment section */
  .comment-section {
    width: 100%;
    max-width: 1000px;
    margin: auto;
    background-color: #eee;
    padding: 10px;
    margin-top: 100px;
    border-radius: 20px;
  }
  
  .user-info {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .user-icon {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #ccc;
    margin-right: 10px;
  }
  
  form {
    margin-top: 20px;
  }
  
  .form-group {
    margin-bottom: 10px;
    margin-right: 12px;
    padding: 5px;;
  }
  
  textarea {
    width: 100%;
    padding: 5px;
    resize: vertical;
  }
  
  .icon-container {
    display: flex;
  }
  
  .icon {
    width: 30px;
    height: 30px;
  }
  
  button[type="submit"] {
    background-color: blue;
    color: white;
    padding: 10px 20px;
    border: none;
    cursor: pointer;
    margin-top: 20px;
    border-radius: 20px;
    width: 130px;
  }
  
  button[type="submit"]:hover {
    background-color: #45a049;
  }
  
  /* Styles for the header and navigation menu */
 
 
  
  /* Styles for the image and camera buttons */
  .images-btn,
  .camera-btn {
    width: 40px;
    height: 40px;
    background-color: #D8D8D8;
    color: black;
    font-size: 25px;
    border: none;
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  /* Custom class to visually hide file input */
  .file-input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    cursor: pointer;
  }
  
  .cv {
    position: relative;
    left: 35%;
  }
  
  .post-button {
    position: relative;
    left: 72%;
  } 
   
  /* Media queries for responsiveness */
  
  @media screen and (max-width: 1088px) {
    .comment-section {
      margin-top: 100px;
      max-width: 800px;
    }
  
    .user-info-label {
      margin-left: 5px;
      margin-bottom: 5px;
    }
  
    .form-group{
      margin-right: 12px;
    }
  
    .user-info {
      display: flex;
      align-items: center;
    }
  
    .user-icon {
      margin-right: 0;
      margin-bottom: 10px;
    }
  
  
    button[type="submit"] {
      margin-top: 12px;
      margin-left: 50%;
      transform: translateX(-50%);
      width: 120px;
    }
  
    .post {
      position: relative;
      left: 30%;
    }
  
    .cv{
      position: absolute;
      left: 0;
    }
  }
  
