.post-cont{
    background-color: white;
    border-radius: 25px;
}
.post-margin-cont{
    margin-top: 162px;
}

.post-image:hover{
    opacity: 0.8; /* Reduce opacity when hovering */
  border: 2px solid #007BFF; /* Add a border when hovering */
  box-shadow: 0px 0px 10px rgba(0, 123, 255, 0.5); /* Add a box shadow when hovering */
  transition: all 0.3s ease;
  cursor: pointer;
}

