    .post a {
        text-decoration: none;
        color: black;
    }
    .post a:hover{
        text-decoration: underline;
        color: blue;
    }




    .post {
        position: relative;
        margin-bottom: 20px;
      }
      
      .post img {
        max-width: 100%;
        cursor: pointer;
      }
      
      .close-button {
        position: absolute;
        top: 10px;
        right: 10px;
        color: #fff;
        cursor: pointer;
        font-size: 18px;
      }
      
      .large-image {
        width: 500px;
        height: 500px;
        object-fit: contain;
      }
      @media (max-width:599px){
        .large-image{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
      }