.highlight-input {
    padding: 10px;
    /* border: 1px solid #ccc; */
    transition: background-color 0.3s; 
    /* Add a smooth transition effect for the background color */
    border: none;
    background-color: #c2cbd370;
}

.highlight-input:focus {
    background-color: #f2f2f2; /* Change the background color when hovered */
    border: 2px solid #3498db;
}

#login-back{
    background:linear-gradient(to right, #a39ef2, #e25d9f);
}


@media screen and (max-width: 768px) {
    .button{
        display: flex;
        justify-content:"center";
        align-items: "center";
    }

    .login-form {
        width: 90%;
    }
}

.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-page {
    overflow: hidden;
    height: 100%;
    margin: 0;
    padding: 0;
}